import React, { useState, useEffect, useRef } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Badge } from '@/components/ui/badge';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import {
  Send,
  Phone,
  Mail,
  X,
  MessageSquare,
  Check,
  CheckCheck,
  AlertCircle,
  Loader2,
  Minimize2,
  Maximize2
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { toast } from 'sonner';
import {
  sendSMSToContact,
  getContactMessages,
  checkNewIncomingMessages,
  formatPhoneToE164,
  formatPhoneForDisplay
} from '@/api/services/messaging/messagingService';
import { getAllPhoneNumbers } from '@/api/services/phoneNumbers/phoneNumberService';

interface Message {
  id: string;
  content: string;
  sender: 'user' | 'contact';
  timestamp: Date;
  status: 'sending' | 'sent' | 'delivered' | 'failed' | 'read';
  messageId?: string;
}

interface Contact {
  id: string;
  email: string;
  contact_number: string;
  list_name?: string;
}

interface ChatSidebarProps {
  isOpen: boolean;
  contact: Contact | null;
  onClose: () => void;
}

const ChatSidebar: React.FC<ChatSidebarProps> = ({ isOpen, contact, onClose }) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [newMessage, setNewMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isSending, setIsSending] = useState(false);
  const [isMinimized, setIsMinimized] = useState(false);
  const [phoneNumbers, setPhoneNumbers] = useState<any[]>([]);
  const [selectedFromNumber, setSelectedFromNumber] = useState<string>('');
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Load message history when contact changes
  useEffect(() => {
    if (contact && isOpen) {
      loadMessageHistory();
    }
  }, [contact, isOpen]);

  // Set up efficient polling for incoming messages only
  useEffect(() => {
    if (!contact || !isOpen || !contact.id) return;

    // Set up polling every 2 seconds for new incoming messages only
    const pollInterval = setInterval(async () => {
      try {
        // Get the last message ID to check for new messages after it
        const lastMessage = messages[messages.length - 1];
        const lastMessageId = lastMessage?.id;

        console.log(`ðŸ” Polling for new incoming messages for contact ${contact.id} after ID: ${lastMessageId}`);

        const response = await checkNewIncomingMessages(contact.id, lastMessageId);

        if (response.success && response.hasNewMessages && response.messages.length > 0) {
          console.log(`ðŸ“¨ Found ${response.messages.length} new incoming messages`);

          // Add new incoming messages to the existing messages
          const newMessages = response.messages.map(msg => ({
            id: msg.id.toString(),
            content: msg.content,
            sender: msg.sender as 'user' | 'contact',
            timestamp: new Date(msg.timestamp),
            status: msg.status as 'sending' | 'sent' | 'delivered' | 'failed' | 'read',
            messageId: msg.messageId
          }));

          setMessages(prev => [...prev, ...newMessages]);

          // Show notification for new messages
          toast.success(`New message from ${contact.email?.split('@')[0] || 'Contact'}`);
        }
      } catch (error) {
        console.error('Error polling for new messages:', error);
        // Don't show error toast to avoid spam
      }
    }, 2000);

    return () => {
      clearInterval(pollInterval);
    };
  }, [contact?.id, isOpen, messages]);

  // Load user's phone numbers
  const loadPhoneNumbers = async () => {
    try {
      const response = await getAllPhoneNumbers();
      if (response.success && response.data) {
        setPhoneNumbers(response.data);
        // Auto-select first phone number if available
        if (response.data.length > 0 && !selectedFromNumber) {
          setSelectedFromNumber(response.data[0].number);
        }
      }
    } catch (error) {
      console.error('Failed to load phone numbers:', error);
    }
  };

  // Load phone numbers when component mounts
  useEffect(() => {
    if (isOpen) {
      loadPhoneNumbers();
    }
  }, [isOpen]);

  // Reset state when sidebar closes
  useEffect(() => {
    if (!isOpen) {
      setMessages([]);
      setNewMessage('');
      setIsMinimized(false);
    }
  }, [isOpen]);

  const loadMessageHistory = async () => {
    if (!contact) return;
    
    setIsLoading(true);
    try {
      const response = await getContactMessages(contact.id);
      if (response.success) {
        const formattedMessages: Message[] = response.messages.map((msg: any) => ({
          id: msg.id.toString(),
          content: msg.content,
          sender: msg.sender,
          timestamp: new Date(msg.timestamp),
          status: msg.status,
          messageId: msg.message_id
        }));
        setMessages(formattedMessages);
      } else {
        setMessages([]);
      }
    } catch (error) {
      console.error('Failed to load message history:', error);
      toast.error('Failed to load message history');
      setMessages([]);
    } finally {
      setIsLoading(false);
    }
  };

  const sendMessage = async () => {
    if (!newMessage.trim() || !contact || isSending) return;

    const messageContent = newMessage.trim();
    setNewMessage('');
    setIsSending(true);

    // Create temporary message with sending status
    const tempMessage: Message = {
      id: Date.now().toString(),
      content: messageContent,
      sender: 'user',
      timestamp: new Date(),
      status: 'sending'
    };

    setMessages(prev => [...prev, tempMessage]);

    try {
      // Format phone number to E.164 format
      const formattedPhone = formatPhoneToE164(contact.contact_number);
      
      // Send SMS using the messaging service with selected phone number
      const result = await sendSMSToContact({
        toPhoneNumber: formattedPhone,
        message: messageContent,
        contactId: contact.id,
        fromPhoneNumber: selectedFromNumber || undefined
      });
      
      if (result.success) {
        // Update message status to sent
        setMessages(prev => 
          prev.map(msg => 
            msg.id === tempMessage.id 
              ? { ...msg, status: 'sent', messageId: result.messageId }
              : msg
          )
        );
        toast.success('Message sent successfully');
      } else {
        throw new Error(result.error || 'Failed to send message');
      }
    } catch (error) {
      console.error('Failed to send message:', error);
      
      // Update message status to failed
      setMessages(prev => 
        prev.map(msg => 
          msg.id === tempMessage.id 
            ? { ...msg, status: 'failed' }
            : msg
        )
      );
      
      toast.error('Failed to send message');
    } finally {
      setIsSending(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const getInitials = (email: string) => {
    const name = email.split('@')[0];
    return name.substring(0, 2).toUpperCase();
  };

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  const getMessageStatusIcon = (status: Message['status']) => {
    switch (status) {
      case 'sending':
        return <Loader2 className="h-3 w-3 animate-spin text-gray-400" />;
      case 'sent':
        return <Check className="h-3 w-3 text-gray-400" />;
      case 'delivered':
        return <CheckCheck className="h-3 w-3 text-blue-500" />;
      case 'failed':
        return <AlertCircle className="h-3 w-3 text-red-500" />;
      default:
        return null;
    }
  };

  if (!contact) return null;

  return (
    <>
      {/* Overlay */}
      {isOpen && (
        <div 
          className="fixed inset-0 bg-black/20 z-40 lg:hidden"
          onClick={onClose}
        />
      )}

      {/* Sidebar */}
      <div className={cn(
        "fixed top-0 right-0 h-full w-full sm:w-1/2 lg:w-2/5 xl:w-1/3 bg-white shadow-2xl z-50 transform transition-transform duration-300 ease-in-out flex flex-col border-l border-gray-200",
        isOpen ? "translate-x-0" : "translate-x-full"
      )}>
        {/* Header */}
        <div className="p-6 border-b bg-gradient-to-r from-blue-50 to-indigo-50 flex-shrink-0">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <Avatar className="h-14 w-14 bg-gradient-to-br from-blue-500 to-indigo-600 shadow-lg">
                <AvatarFallback className="text-white font-semibold text-lg">
                  {getInitials(contact.email)}
                </AvatarFallback>
              </Avatar>
              <div>
                <h3 className="font-bold text-gray-900 text-xl">
                  {contact.email.split('@')[0]}
                </h3>
                <div className="flex flex-col space-y-2 text-sm text-gray-600">
                  <div className="flex items-center space-x-2">
                    <Mail className="h-4 w-4 text-blue-500" />
                    <span className="truncate font-medium">{contact.email}</span>
                  </div>
                  <div className="flex items-center space-x-2">
                    <Phone className="h-4 w-4 text-green-500" />
                    <span className="font-medium">{formatPhoneForDisplay(contact.contact_number)}</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="flex items-center space-x-3">
              {contact.list_name && (
                <Badge variant="secondary" className="text-xs bg-blue-100 text-blue-700 border-blue-200">
                  {contact.list_name}
                </Badge>
              )}
              <Button
                variant="ghost"
                size="sm"
                onClick={() => setIsMinimized(!isMinimized)}
                className="h-9 w-9 p-0 hover:bg-blue-100 rounded-full"
              >
                {isMinimized ? <Maximize2 className="h-4 w-4" /> : <Minimize2 className="h-4 w-4" />}
              </Button>
              <Button
                variant="ghost"
                size="sm"
                onClick={onClose}
                className="h-9 w-9 p-0 hover:bg-red-100 text-gray-600 hover:text-red-600 rounded-full"
              >
                <X className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </div>

        {!isMinimized && (
          <>
            {/* Messages Area */}
            <div className="flex-1 overflow-y-auto p-6 bg-gradient-to-b from-gray-50 to-white">
              {messages.length === 0 ? (
                <div className="flex flex-col items-center justify-center h-full text-gray-400">
                  <div className="w-16 h-16 bg-white rounded-full flex items-center justify-center mb-4 shadow-sm">
                    <MessageSquare className="h-8 w-8" />
                  </div>
                  <p className="text-center text-sm">
                    No messages yet. Start a conversation!
                  </p>
                </div>
              ) : (
                <div className="space-y-4">
                  {messages.map((message) => (
                    <div
                      key={message.id}
                      className={cn(
                        'flex',
                        message.sender === 'user' ? 'justify-end' : 'justify-start'
                      )}
                    >
                      <div
                        className={cn(
                          'max-w-[80%] rounded-lg px-4 py-2 shadow-sm',
                          message.sender === 'user'
                            ? 'bg-primary text-primary-foreground'
                            : 'bg-white text-gray-900 border'
                        )}
                      >
                        <p className="text-sm">{message.content}</p>
                        <div className={cn(
                          'flex items-center justify-end space-x-1 mt-1',
                          message.sender === 'user' ? 'text-primary-foreground/70' : 'text-gray-500'
                        )}>
                          <span className="text-xs">{formatTime(message.timestamp)}</span>
                          {message.sender === 'user' && getMessageStatusIcon(message.status)}
                        </div>
                      </div>
                    </div>
                  ))}
                  <div ref={messagesEndRef} />
                </div>
              )}
            </div>

            {/* Message Input */}
            <div className="p-6 border-t bg-white flex-shrink-0 shadow-lg">
              {/* Phone Number Selector */}
              {phoneNumbers.length > 0 && (
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Send from phone number:
                  </label>
                  <Select value={selectedFromNumber} onValueChange={setSelectedFromNumber}>
                    <SelectTrigger className="w-full">
                      <SelectValue placeholder="Select phone number" />
                    </SelectTrigger>
                    <SelectContent>
                      {phoneNumbers.map((phone) => (
                        <SelectItem key={phone.id} value={phone.number}>
                          <div className="flex items-center space-x-2">
                            <span>{formatPhoneForDisplay(phone.number)}</span>
                            {phone.friendlyName && (
                              <span className="text-gray-500 text-sm">({phone.friendlyName})</span>
                            )}
                          </div>
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              )}

              <div className="flex items-center space-x-3">
                <Input
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  onKeyPress={handleKeyPress}
                  placeholder="Type your message..."
                  className="flex-1 border-gray-300 focus:border-blue-500 focus:ring-blue-500 rounded-lg"
                  disabled={isSending}
                />
                <Button
                  onClick={sendMessage}
                  disabled={!newMessage.trim() || isSending || !selectedFromNumber}
                  size="sm"
                  className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg shadow-md"
                >
                  {isSending ? (
                    <Loader2 className="h-4 w-4 animate-spin" />
                  ) : (
                    <Send className="h-4 w-4" />
                  )}
                </Button>
              </div>
              <p className="text-xs text-gray-500 mt-3 flex items-center space-x-1">
                <MessageSquare className="h-3 w-3" />
                <span>
                  {selectedFromNumber
                    ? `Sending from ${formatPhoneForDisplay(selectedFromNumber)} to ${formatPhoneForDisplay(contact.contact_number)}`
                    : `Messages will be sent via SMS to ${formatPhoneForDisplay(contact.contact_number)}`
                  }
                </span>
              </p>
            </div>
          </>
        )}
      </div>
    </>
  );
};

export default ChatSidebar;
